	module.exports = {"data":{"markdownRemark":{"html":"<p>電車の中で久々に強烈な夢を見た。そして乗り過ごしたorz 何が起きたか突然幻想郷に迷いこんで、あっちこっちさまよい辛うじて博麗神社を発見。これで帰れる、と歓喜の気持ちで神社に入るが誰もいない上に帰り方も分からない。ちょっと焦ってきて賽銭箱に10万円放り込んで手をあわせた瞬間何故か無性にあぃ～あぃ～したくなりだす。よく見たら博麗神社じゃなくて 博ディ麗神社だった。まるで呪われたかのようにあぃ～あぃ～が止まらなくなって泣きそうになったら、奥から霊夢が出てきて霊夢まで一緒にあぃ～あぃ～しだして、魔理沙が遊びにあぃ～来たと思ったら魔理沙もあぃ～あぃ～しだして、なんかスキマからあぃ～あぃ～聞こえたとあぃ～思ったら、中からあぃ～ディレイラマがゴゴゴあぃ～ゴゴってあぃ～効果音と共にあぃ～登場してあぃ～あぃ～あぃ～あぃ～あぃ～あぃ～あぃ～</p>\n<h2>痛勤</h2>\n<p>こりゃSEが小太りか劇痩せする理由が目に見えて分かるぜ…orz 会社を出たのは21時半、家に着いたは23時半、そして朝出る時間は6時過ぎ……うーんあるある……<strong>ねぇよ１！！</strong></p>\n<h2>本日の更新</h2>\n<p>東方弾幕風フレームワークライブラリ『<a href=\"/tag/flan\">Framework of LunAtic Notation</a>』のバージョン0.1eをリリースしました。今回はボスがスペル発動中以外でも背景が有効になるオプションを追加、加えて使い魔周りのバグ修正をしました。</p>\n<h2>(拍)danmaqさんってキスしたことありますか？(///)</h2>\n<p>悪いが今のところ彼女イナイ歴1年未満保ってるぜ？</p>\n<h2>(拍)夢終劇アリスに萌えた！もとい燃えた！</h2>\n<p>実はウチもマリ×アリ派。(マリ×香霖じゃなかったっけ？って質問はナシだぜ？)</p>\n<h2>(拍)夢終劇のラスボスって霊夢ですか？</h2>\n<p>ネタバレには答えかねますが、たぶん皆さんの予想のはるか斜め上-360度(一周しちゃった)辺りの顛末が待っていますのでどうか期待していただければありがたいです。</p>\n<h2>(拍)Strutsって何でしたっけ？…すみません弾幕風初心者なもので</h2>\n<p>っ<a href=\"http://www.google.co.jp/search?q=Apache+Struts\">Google</a> そもそもStrutsは例え話として出しただけで弾幕風とは一切関係ないですよ？</p>","frontmatter":{"cover":null,"date":"2006-08-23T21:43:00+09:00","strDate":"2006/8/23","title":"帰りの電車で見た夢"}}},"pathContext":{"path":"/ja/blog/2006/08/23-帰りの電車で見た夢/","langKey":"ja"}}

